@font-face {
  font-family: "JetBrainsMono";
  src: url("../fonts/JetBrainsMono.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.slider-place {
  display: flex;
  width: 80vw;
  height: auto;
  margin-inline: 10vw auto;
  margin-bottom: 2vw;
  padding-top: 5vw;
  padding-bottom: 4vw;
  background-color: #ffffff;
  flex-direction: column;
  text-align: center;
}

.slider-place>h2 {
  font-family: "JetBrainsMono";
  font-weight: bold;
  font-size: 3vw;
  color: #919682;
  white-space: pre;
  margin-bottom: 2vw;
}

.slider-place-content {
  display: flex;
  width: 100%;
  height: auto;
  /* margin-inline: 10vw auto;
  margin-bottom: 2vw;
  padding-top: 5vw;
  padding-bottom: 4vw;
  background-color: #ffffff; */
  justify-content: center;
  align-items: center;
  text-align: center;
}

.slider-image {
  width: 100%;
  height: auto;
  border-radius: 30px;
}

.slider-btn-prev,
.slider-btn-next {
  width: 8vw;
  height: 4vw;
  border: 1px solid #595e48;
  background-color: #595e48;
  color: #ece1dd;
  z-index: 2;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}

.slider-btn-prev {
  margin-right: -4vw;
  border-radius: 15px 0 0 15px;
}

.slider-btn-prev > img,
.slider-btn-next > img {
  width: 1.5vw;
  height: auto;
}

.slider-btn-next {
  margin-left: -4vw;
  border-radius: 0 15px 15px 0;
}

.slider-btn-prev:hover,
.slider-btn-next:hover {
  background-color: #404238;
  border: 1px solid #404238;
}

@media (max-width: 768px) {
  .slider-place {
    width: 90%;
    height: 80vw;
    padding-top: 10vw;
    padding-bottom: 10vw;
    margin-inline: 5% auto;
    margin-top: -8vw;
    overflow: hidden;
  }

  .slider-place>h2 {
    font-size: 5vw;
    font-weight: normal;
  }

  .slider-image {
    width: 120vw;
  }

  .slider-btn-prev,
  .slider-btn-next {
    width: 10vw;
    height: 10vw;
    border-radius: 5vw;
    position: absolute;
  }

  .slider-btn-prev > img,
  .slider-btn-next > img {
    width: 3vw;
    height: auto;
  }

  .slider-btn-prev {
    margin: 0;
    left: 7vw;
  }

  .slider-btn-next {
    margin: 0;
    right: 7vw;
  }
}
