@font-face {
    font-family: "JetBrainsMono";
    src: url("../fonts/JetBrainsMono.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
}

.countdown {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2vw;
    font-family: 'JetBrainsMono';
    text-align: center;
    margin-top: 4vw;
}

.countdown-item {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.countdown-number {
    font-size: 3vw;
    font-weight: bold;
    color: #919682;
}

.countdown-label {
    font-size: 1vw;
    margin-top: 0.3vw;
    color: #919682;
}

@media (max-width: 768px) {
    .countdown {
        gap: 5vw;
    }

    .countdown-number {
        font-size: 7vw;
        font-weight: bold;
        color: #919682;
    }
    
    .countdown-label {
        font-size: 2vw;
        margin-top: 0.3vw;
        color: #919682;
    }
}