@font-face {
    font-family: "JetBrainsMono";
    src: url("../fonts/JetBrainsMono.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
}

.time-table {
    display: flex;  
    width: 80vw;
    height: auto;
    margin-inline: 10vw auto;
    margin-block: -30px;
    border-radius: 30px;
    padding-block: 90px;
    background-color: #FFFFFF;
    flex-direction: column;
}

.time-table-el-time {
    width: auto;
    height: auto;
    display: flex;
    /* border-right: 5px dashed green; */
    border-bottom: 5px dotted #C8CAC0;
    /* border-radius: 0 0 15px 0; */
    justify-content: center;
}

.time-table-el-time>h2 {
    font-family: "JetBrainsMono";
    font-size: 2.5vw;
    font-weight: bold;
    color: #919682;
}

.time-table-el>h2 {
    font-family: "JetBrainsMono";
    font-size: 1.7vw;
    color: #919682;
}

.time-table-round {
    width: 2vw;
    height: 2vw;
    border-radius: 1vw;
    background-color: #919682;
    margin-left: 50%;
}

.time-table-content {
    display: flex;
    flex-direction: row;
}

.time-table-left {
    width: calc(50% + 1vw);
    height: auto;
    border-right: 3px solid #919682;
}

.time-table-right {
    width: calc(50% - 1vw);
    height: auto;
}

@media (max-width: 768px) {
    .time-table {
        width: 90%;
        margin-inline: 5% auto;
    }

    .time-table-el-time>h2 {
        font-size: 4vw;
    }
    
    .time-table-el>h2 {
        font-size: 2.5vw;
    }

    .time-table-left {
        border-right: 2px solid #919682;
    }
}