@font-face {
    font-family: "GreatVibes";
    src: url("../fonts/GreatVibes.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
}

.ivite-content {
    display: flex;  
    width: 80vw;
    height: auto;
    margin-inline: 10vw auto;
    margin-block: -30px;
    border-radius: 30px;
    background-color: #F5EBE9;
    justify-content: center;
    align-items: center;
    text-align: center; 
    z-index: 2;
}

.ivite-content>blockquote {
    font-family: "GreatVibes";
    color: #AF7E84;
    font-size: 3vw;
    border: 2px solid #AF7E84;
    border-radius: 30px;
    margin-block: 4vw;
    padding-block: 4vw;
    padding-inline: 18vw;
}

@media (max-width: 768px) {
    .ivite-content {
        width: 90%;
        margin-inline: 5% auto;
    }

    .ivite-content>blockquote {
        font-size: 5vw;
        margin-block: 4vw;
        padding-block: 4vw;
        padding-inline: 8vw;
        border: 1px solid #AF7E84;
        line-height: 6vw;
    }
}