@font-face {
  font-family: "GreatVibes";
  src: url("../fonts/GreatVibes.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "JetBrainsMono";
  src: url("../fonts/JetBrainsMono.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.content-with-border {
  width: 80vw;
  height: 50vw;
  margin-inline: 10vw auto;
  background-color: #fff;
  /* border-radius: 15px; */
  /* box-shadow: 0 1px 2px #000; */
  overflow: hidden;
  display: flex;
  flex-flow: row;
}

.content-with-border-left {
  margin-left: 4vw;
  margin-bottom: 30px;
  transform: scaleX(-1);
}

.content-main {
  position: absolute;
  width: 40%;
  height: auto;
  padding-block: 6% 6%;
  left: 45%;
}

.content-main-info {
  display: flex;
}

.content-main-info-date {
  margin-top: 2.5vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5vw;
}

.content-main-info-date > h2 {
  font-family: "GreatVibes";
  color: #c7a491;
  font-size: 3vw;
  margin: 0;
}

.content-main-info-and {
  width: 5vw;
  margin-top: 3vw;
}

.content-main-info-and > h1 {
  font-size: 17vw;
  margin: 0;
  margin-top: -4vw;
  opacity: 0.5;
  color: #919682;
}

.content-main-info-names {
  margin-top: 3.5vw;
  line-height: 7vw;
  z-index: 2;
}

.content-main-info-names-grooms {
  color: #595e48;
}

.content-main-info-names-bride {
  color: #eecfcc;
}

.content-main,
.content-main-info,
.content-main-info-date,
.content-main-info-names {
  justify-content: center;
  text-align: center;
}

.content-main > h1 {
  font-size: 3.5vw;
  color: #919682;
  margin-bottom: 2vw;
}

.content-main-info-names > h2 {
  font-size: 8vw;
}

.content-main > h1,
.content-main-info-and > h1 {
  font-family: "JetBrainsMono";
  white-space: pre;
}

.content-main-info-names > h2 {
  font-family: "GreatVibes";
  font-weight: 300;
  font-style: normal;
}

@media (max-width: 768px) {
  .content-with-border-right,
  .content-with-border-left,
  .empty-elemnt {
    display: none;
  }

  .content-with-border {
    width: 90%;
    height: 80vw;
    margin-inline: 5% auto;
    padding-bottom: 10vh;
    background-color: #fff;
    /* box-shadow: 0 1px 2px #000; */
    overflow: hidden;
    display: flex;
    flex-flow: row;
  }

  .content-main {
    width: 70%;
    height: 40%;
    top: 5%;
    left: 15%;
  }

  .content-main > h1 {
    font-size: 7vw;
  }

  .content-main-info-date > h2 {
    margin-top: 3vh;
    font-size: 6vw;
  }

  .content-main-info-and > h1 {
    font-size: 25vw;
  }

  .content-main-info-names {
    margin-top: 9vw;
    line-height: 12vw;
  }

  .content-main-info-names > h2 {
    font-size: 12vw;
  }
}
